'use client'; // needed for hooks in Next.js App Router

import { useEffect, useState } from 'react';
import axios from 'axios';

export default function Home() {
  const [user, setUser] = useState(null);

  const fetchUser = async () => {
    try {
      const res = await axios.get('http://localhost:1337/api/user');
      console.log(res);
      // Adjust depending on Strapi response shape
      const firstUser = res.data[0]; // no .data.data[0].attributes
      setUser(firstUser);

    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div>
      <h1>Main Screen</h1>
      {user ? (
        <p>First user email: {user.email}</p>
      ) : (
        <p>No user loaded yet</p>
      )}
      <button onClick={fetchUser}>Load User</button>
    </div>
  );

}